import { AppShell } from "@/components/layout/app-shell";
import { GlassCard } from "@/components/layout/glass-card";
import Link from "next/link";

export default function SettingsPage() {
  return (
    <AppShell activeTab="/dashboards">
      <div className="space-y-4 max-w-2xl">
        <h2 className="text-xl font-semibold">Settings</h2>
        <GlassCard>
          <h3 className="text-card-title font-medium mb-2">App</h3>
          <p className="text-sm text-gray-600 dark:text-gray-300">
            Theme and general options are available in the header. Connection to Home Assistant is managed in{" "}
            <Link href="/onboarding" className="text-accent-purple dark:text-accent-green underline">
              onboarding
            </Link>.
          </p>
        </GlassCard>
      </div>
    </AppShell>
  );
}
